* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

html,body {
  color: white;
  background-color: aliceblue;
  font-family: Lato, sans-serif;
  margin-top: 40px;

}

h1{
  color: #1b1b32;
  font-size: 25px;
  font-family: Roboto, sans-serif;
  text-align: center;
  margin: 20px 0;
}

.aplicacion-tareas{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.sergio-logo-contenedor{
  width: 100%;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sergio-logo {
  width: 400px;
  height: 50px;

}

.tareas-lista-principal{
  width: 600px;
  min-height: 500px;
  background-color: #d0d0d5;
  padding: 25px;
  margin: 10px;
  border-radius: 25px;
}

